@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
    overflow-x: hidden;
    background-color: black;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: antiquewhite;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

#root {
    overflow-x: hidden;
}

@media screen and (max-width:960px) {
    body {
        overflow-x: hidden;
    }
}
.btn {
    background-image: linear-gradient(35deg, rgb(248, 112, 0), rgb(255, 40, 255));
    color: rgb(240, 240, 240);
    border: none;
    outline: none;
    border-radius: 5px;
    transition: 300ms all ease;
}

.btn:hover {
    background-image: none;
    background-color: transparent;
    border: 1px solid rgb(248, 112, 0);
    letter-spacing: 1PX;
}

.btn-medium {
    padding: 8px 12px;
    font-size: 20px;
}

.btn-large {
    padding: 12px 20px;
    font-size: 20px;
    font-weight: 600;
}
.header {
    position: relative;
    background-color: black;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 100px;
}

.header .logo {
    margin-right: auto;
    margin-left: 4%;
    font-size: 2.5rem;
    font-weight: 700;
    text-decoration: none;
    background: linear-gradient(35deg, rgb(248, 112, 0), rgb(255, 40, 255));
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
}

.nav--links {
    font-family: 'Poppins', sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
}

.links {
    font-size: 1.05rem;
    font-weight: 500;
    transition: 300ms all ease;
}

.links:hover {
    transition: 300ms all ease;
    background: linear-gradient(35deg, rgb(248, 112, 0), rgb(255, 40, 255));
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
}

.nav--links ul li {
    list-style: none;
    display: inline-block;
    padding: 5px;
}

.text-white {
    color: rgb(255, 249, 241);
    text-decoration: none;
}

.button-container {
    position: relative;
    width: 15%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.active {
    border-bottom: 1px solid rgb(255, 187, 0);
}

.mobile-button-container {
    display: none;
}

.menu-bar {
    display: none;
}

@media screen and (max-width:960px) {
    .nav--links {
        top: 100px;
        background-color: black;
        position: absolute;
        height: 90vh;
        width: 100vw;
        right: -100%;
        transition: 300ms all ease;
    }
    .nav--links ul li {
        display: block;
        padding: 20px;
    }
    .button-container {
        display: none;
    }
    .mobile-button-container {
        display: block;
    }
    .menu-bar {
        display: block;
        margin-right: 20px;
        font-size: 1.5rem;
    }
    .nav--links--active {
        right: 0;
        z-index: 10000000000;
    }
}
.hire-me {
    padding: 30px 15%;
    background-image: linear-gradient(to bottom, rgb(25, 25, 25), rgb(0, 0, 0));
    background-color: rgb(34, 34, 34);
}

.hire-me h1,
.hire-me h2,
.hire-me p {
    text-align: center;
}

.hire-me p {
    font-size: 1.2rem;
    margin-top: -20px;
}

@media screen and (max-width:960px) {
    .hire-me {
        padding: 20px 10%;
    }
}
.hire-list-container {
    margin: 20px auto;
    height: 400px;
    width: 400px;
    display: grid;
    grid-template-areas: 'a b' 'c d';
    border-radius: 8px;
    overflow: hidden;
}

.hire-fiver,
.hire-upwork,
.hire-freelancer,
.hire-devtasir {
    height: 200px;
    width: 200px;
    cursor: pointer;
    transition: 300ms all ease;
    overflow: hidden;
}

.hire-fiver {
    grid-area: a;
    background-color: blueviolet;
}

.hire-upwork {
    grid-area: d;
    background-color: rgb(235, 206, 42);
}

.hire-freelancer {
    grid-area: c;
    background-color: rgb(15, 201, 207);
}

.hire-devtasir {
    grid-area: b;
    background-color: rgb(226, 43, 43);
}

.hire-list-container div img {
    width: 100%;
    height: 100%;
}

.hover-effect {
    cursor: pointer;
}

.hover-image-effect {
    transform: scale(1.2) rotate(10deg);
    transition: 500ms all ease;
}

.image {
    transition: 500ms all ease;
}

@media screen and (max-width:960px) {
    .hire-list-container {
        height: 300px;
        width: 300px;
    }
    .hire-fiver,
    .hire-upwork,
    .hire-freelancer,
    .hire-devtasir {
        height: 150px;
        width: 150px;
    }
}
