.card-container {
    cursor: pointer;
    position: relative;
    margin: 15px;
    height: 280px;
    width: 280px;
    box-shadow: 2px 2px 5px #ff0095b7;
    background-color: rgba(0, 0, 0, 0.99);
    border-radius: 8px;
    overflow: hidden;
}

.card-container img {
    width: 100%;
    height: 100%;
}

.extra-info {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: 300ms all ease;
    transform: scale(0.7);
    opacity: 0;
}

.extra-info p {
    text-align: center;
}

.anim {
    transform: scale(1);
    opacity: 1;
}

.image {
    transform: scale(1.3);
    opacity: 1;
    transition: 300ms all ease;
    height: 100%;
    width: 100%;
}

.not-active {
    transform: scale(1.08);
    opacity: 0.6;
}
